import React from 'react';

class InputField extends React.Component {
  constructor(props) {
    super(props);
  }


  render() {
    const { value, error , inputType, placeholder, inputRef, onChange } = this.props
    return (
      <div className={`form-group ${error ? 'has-error' : ''}`}>
        <input  ref="email" 
                type={inputType} 
                className="form-control"
                id="inputEmail" 
                placeholder={placeholder}
                ref={inputRef}
                onChange={onChange}/>
        <p className="error">
          {error}  
        </p>
      </div>
    );
  }
}


export default InputField;
